body {
    margin: 0;
    margin-top: 5%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #303030;
    color: #FFFFFF;
    background-image: url('https://i1.wp.com/blog.flipsidecrypto.com/wp-content/uploads/2021/11/SolanaSpin_Notion_01.gif?resize=682%2C436&ssl=1');
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.mint-container {
    border-style: solid;
    border-width: 3px;
    border-color: purple;
    border-radius: 30px 30px 30px 30px;
    width: 300px;
    text-align: center;
    padding-top: 3%;
    padding-bottom: 5%;
    font-size: 1.3em;
    margin: auto;
    margin-top: 5%;
}